@import 'tailwindcss/base';
@import 'tailwindcss/components';
@import 'tailwindcss/utilities';

body {
  @apply bg-gray-900 text-white;
}

.registration-form {
  @apply max-w-md mx-auto p-6 bg-gray-800 rounded-lg shadow-xl;
}

.form-input {
  @apply w-full px-3 py-2 text-white bg-gray-700 rounded-md focus:outline-none focus:ring-2 focus:ring-purple-600;
}

.btn {
  @apply px-4 py-2 font-bold text-white bg-purple-600 rounded-md hover:bg-purple-700 focus:outline-none focus:ring-2 focus:ring-purple-600;
}