@tailwind base;
@tailwind components;
@tailwind utilities;

.profile-completion-arc {
  transform: rotate(-90deg);
  transform-origin: 50% 50%;
}

/* Prevent touch highlight */
* {
  -webkit-tap-highlight-color: transparent;
  user-select: none;
}

/* Custom scrollbar */
::-webkit-scrollbar {
  width: 0px;
}

/* Prevent elastic scroll on iOS */
html, body {
  position: fixed;
  overflow: hidden;
  width: 100%;
  height: 100%;
  overscroll-behavior-y: none;
  background: black;
}

#root {
  height: 100%;
  overflow-y: auto;
  overscroll-behavior-y: contain;
  -webkit-overflow-scrolling: touch;
}

/* Stars animation */
@keyframes twinkle {
  0% { opacity: 0; }
  50% { opacity: 1; }
  100% { opacity: 0; }
}

.stars-small,
.stars-medium,
.stars-large {
  position: fixed;
  width: 100%;
  height: 100%;
  background: transparent;
  pointer-events: none;
}

.stars-small::after,
.stars-medium::after,
.stars-large::after {
  content: "";
  position: absolute;
  width: 100%;
  height: 100%;
  animation: twinkle 2s infinite;
}

.stars-small::after {
  background-image: radial-gradient(1px 1px at 50% 50%, white, rgba(0,0,0,0));
  background-size: 50px 50px;
}

.stars-medium::after {
  background-image: radial-gradient(1.5px 1.5px at 50% 50%, white, rgba(0,0,0,0));
  background-size: 100px 100px;
  animation-delay: 0.5s;
}

.stars-large::after {
  background-image: radial-gradient(2px 2px at 50% 50%, white, rgba(0,0,0,0));
  background-size: 150px 150px;
  animation-delay: 1s;
}

