@tailwind base;
@tailwind components;
@tailwind utilities;

:root {
  --app-height: 100%;
}

html, body, #root {
  @apply bg-black text-white;
  margin: 0;
  padding: 0;
  overflow: hidden;
  position: fixed;
  width: 100%;
  height: 100%;
  height: var(--app-height);
}

.onboarding-container {
  @apply fixed inset-0 flex flex-col;
  height: var(--app-height);
}

/* Stars animation styles remain the same */
.stars-small,
.stars-medium,
.stars-large {
  position: fixed;
  width: 100%;
  height: 100%;
  background: transparent;
  pointer-events: none;
}

/* ... rest of stars styles remain the same ... */

/* Updated navigation pills styles */
.nav-pills-container {
  @apply w-full max-w-screen-sm mx-auto px-4;
}

.nav-pills {
  @apply flex justify-center gap-2 w-full;
  overflow-x: auto;
  -webkit-overflow-scrolling: touch;
  scrollbar-width: none;
  -ms-overflow-style: none;
  mask-image: linear-gradient(to right, transparent, black 20px, black 90%, transparent);
}

.nav-pills::-webkit-scrollbar {
  display: none;
}

.nav-pill {
  @apply px-3 py-1 rounded-full text-xs whitespace-nowrap transition-colors flex-shrink-0;
}

/* Updated content container */
.content-container {
  @apply flex-1 w-full h-full max-w-screen-sm mx-auto px-4 flex flex-col;
  height: calc(var(--app-height) - 120px); /* Adjust based on your header and footer height */
}

.content-wrapper {
  @apply flex-1 flex flex-col justify-between;
}

.next-button {
  @apply mx-auto flex items-center justify-center gap-2 
         bg-[#4C9EEB] text-white rounded-xl py-3 hover:bg-[#4C9EEB]/90 transition-colors
         w-full max-w-[400px];
}

/* Mobile optimizations */
@media (max-width: 640px) {
  .content-container {
    @apply px-3;
    height: calc(var(--app-height) - 100px);
  }

  .nav-pills-container {
    @apply px-2;
  }

  h1 {
    @apply text-2xl;
  }

  .next-button {
    @apply py-2 text-sm;
  }
}

