@tailwind base;
@tailwind components;
@tailwind utilities;

:root {
  --app-height: 100%;
}

html,
body {
  padding: 0;
  margin: 0;
  overflow: hidden;
  width: 100vw;
  height: 100vh;
  height: var(--app-height);
}

.app-container {
  width: 100%;
  height: 100%;
  background-color: black;
  color: white;
  display: flex;
  flex-direction: column;
  padding-bottom: calc(env(safe-area-inset-bottom) + 5rem); /* Added padding for navigation */
}

.main-content {
  flex-grow: 1;
  position: relative;
  overflow: hidden;
}

.nav-container {
  z-index: 50;
  padding-bottom: max(1.5rem, env(safe-area-inset-bottom)); /* Ensures proper spacing on devices with home indicators */
}

.nav-content button {
  min-height: 44px; /* Minimum touch target size */
  min-width: 44px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.card-enter {
  transform: translateX(0) rotate(0);
  opacity: 1;
}

.card-exit-left {
  transform: translateX(-100%) rotate(-12deg);
  opacity: 0;
}

.card-exit-right {
  transform: translateX(100%) rotate(12deg);
  opacity: 0;
}